import React, { useState } from 'react';
import { FormControlLabel, Drawer, Switch, IconButton, List, ListItem } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';

interface MenuProps {
  currentPart: string;
  changeCurrentPart: (val:string) => void;
  slides: {
    part: string;
    word: string;
    partOfSpeech: string;
    trans: string;
    sentence: string;
    sentenceTrans: string;
  }[];
}

const Menu: React.FC<MenuProps> = (props) => {
  const [menuToggle, setMenuToggle] = useState(false);

  const toggleDrawer = () => {
    setMenuToggle(!menuToggle);
  };

  const slides = props.slides;
  const parts = slides.reduce((val:string[], cur) => {
    if (!val.includes(cur.part)) {
      val.push(cur.part);
    }
    return val;
  }, []);

  return (
    <>
      <IconButton
        color="inherit"
        aria-label="open drawer"
        edge="end"
        onClick={() => toggleDrawer()}
      />
      <Drawer
        anchor = {'left'}
        open = {menuToggle}
        onClose = {() => setMenuToggle(false)}
      >
        <div>
          <List>
            <ListItem>
              <FormControlLabel
                value="Theme"
                control={<Switch color="primary" />}
                label="Theme"
                labelPlacement="start"
              />
              </ListItem>
          </List>
          <Divider />
          <List component="nav" aria-label="secondary mailbox folders">
            {
              parts.map(p => {
                const cur = p === props.currentPart ? '(c)': '';
                return (
                  <ListItem 
                    button
                    onClick={() => props.changeCurrentPart(p)}
                    key={p}>
                    <ListItemText primary={p+cur}/>
                  </ListItem>
                )
              })
            }
          </List>
        </div>
      </Drawer>
      <MenuIcon />
    </>
  );
}

export default Menu;